.home {
    max-width: 1500px;
    margin: 0 auto;
}

.home__image {
    width: 100%;
    mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
    margin-bottom: -150px;
    z-index: -1;
    height: 450px;
    object-fit: cover;
}

.home__row {
    display: flex;
    margin-left: 5px;
    margin-right: 5px;
    z-index: 1;
}